// Fonts
@import url('https://fonts.googleapis.com/css?family=Biryani|Muli|Prompt|Open+Sans|Roboto+Mono|Work+Sans:800');

// Variables
@import "variables";

a {
    text-decoration: none;
    color: inherit; }

body {
    background-color: #fff;
    font-family: 'Muli', sans-serif;
    overflow-y: auto;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    background-color: #f7f9fa; }

nav {
    background-color: #191e28;
    display: flex;
    justify-content: center;
    overflow: hidden;
    margin-bottom: 20px;

    .logo {
        position: relative;
        font-size: 45px;
        margin-right: 20px;
        padding: 10px 0 10px 0;
        transition: all .1s ease-in-out;

        .spinner {
            position: absolute;
            top: calc(42% - 50px);
            left: calc(50% - 50px);
            z-index: 0;
            height: 100px;
            width: 100px;
            border-radius: 50%;
            border-bottom: 10px solid #d9534f;
            border-left: 10px solid #d9534f;
            border-right: 10px solid transparent;
            border-top: 10px solid transparent;
            animation: spinForwards 1s linear infinite;

            span {
                position: absolute;
                top: calc(35% - 35px);
                left: calc(50% - 50px);
                z-index: 0;
                height: 80px;
                width: 80px;
                border-radius: 50%;
                border-top: 10px solid rgba(217,83,79, .6);
                border-left: 10px solid rgba(217,83,79, .6);
                border-right: 10px solid transparent;
                border-bottom: 10px solid transparent;
                animation: spinForwards 3s .2s linear infinite; } }


        .text {
            position: relative;
            z-index: 1; } }

    .links {
        margin-left: 20px;
        padding: 10px 0 10px 0;

        ul {
            margin: 0;
            padding: 0;
            display: flex;
            list-style-type: none;
            height: 100%;

            .link {
                color: white;
                height: 100%;
                display: flex;
                padding: 0 20px;
                align-items: center;
                cursor: pointer;
                position: relative;

                span:nth-child(1) {
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 5px;
                    width: 0;
                    background-color: #d9534f;
                    pointer: cursor; }

                &:hover > span {
                    width: 100%;
                    transition: all .1s ease-in-out; } } } }

    &:hover {
        .logo {
            padding: 40px 0 40px 0;
            transition: all .1s ease-in-out; } } }

.loader {
  height: 100px;
  width: 100px;
  margin: 50px; }


.loader1 {
  height: 100%;
  width: 100%;
  position: relative;

  div:nth-child(1) {
    top: 0;
    left: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    border: 10px solid #d9534f;
    border-left: 10px solid transparent;
    animation: spinForwards 2s linear infinite; }

  div:nth-child(2) {
    top: 0;
    left: 0;
    margin-left: 10%;
    margin-top: 10%;
    position: absolute;
    height: 80%;
    width: 80%;
    border-radius: 50%;
    border: 10px solid rgba(217,83,79, .6);
    border-left: 10px solid transparent;
    border-bottom: 10px solid transparent;
    animation: spinForwards 1s linear infinite; } }


.menu-icon {
    display: none;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    position: relative;
    width: 75px;

    .menu-icon-middle {
        position: relative;
        width: 35px;
        height: 5px;
        margin-bottom: 6px;

        span {
            position: absolute;
            transition: all .2s ease-in-out; } }


    span {
        display: block;
        width: 35px;
        height: 5px;
        background-color: white;
        margin: 3px 0;
        transition: all .2s ease-in-out; }

    &.active {
        & > span {
            opacity: 0;
            transition: all .2s ease-in-out; }

        .menu-icon-middle {
            span:nth-child(1) {
                transform: rotate(45deg);
                transition: all .2s ease-in-out; }

            span:nth-child(2) {
                transform: rotate(-45deg);
                transition: all .2s ease-in-out; } } } }


.mobile-links {
    position: absolute;
    top: -250px;
    left: 0;
    z-index: -1;
    height: 250px;
    background-color: #f7f9fa;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s ease-out;


    ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
        width: 100%; }

    li {
        padding: 13px 0;
        width: 100%;
        border-top: 2px solid darken(#f7f9fa, 5);
        text-align: center; }

    &.active {
        top: 50px;
        transition: all .3s ease-in; } }

.container {
    position: relative;
    z-index: -10; }

@media only screen and (max-width : 768px) {
    .menu-icon {
        display: flex; }

    nav {
        .logo {
            text-align: center;
            font-size: 30px; } }

    .links {
        display: none; } }

@media only screen and (max-width : 992px) {
    nav {
        //justify-content: space-between
        //padding: 0 20px

        .logo {
            font-size: 30px; }

        .links {
            font-size: 14px; }

        &:hover .logo {
            padding: 10px 0 10px 0; }

        .mobile-links {
            display: block; } } }

@keyframes spinForwards {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@import "index";
@import "post";
